import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
const axios = require('axios');

function App() 
{
  const [clickCount,setClickCount]=useState(0)
  const [clickPlanets,setClickPlanets]=useState([])
  const [planetList,setPlanetList]=useState([])
  const [vehicleList,setVehicleList]=useState([])
  const [card1,setCard1]=useState([])
  const [card2,setCard2]=useState([])
  const [card3,setCard3]=useState([])
  const [card4,setCard4]=useState([])
  const [radio1,setRadio1]=useState()
  const [radio2,setRadio2]=useState()
  const [radio3,setRadio3]=useState()
  const [radio4,setRadio4]=useState()

  useEffect(()=>{
    let imgList=[]
    axios.get("https://findfalcone.herokuapp.com/planets").then(res=>{
      let data=res.data
      var child={}
      var i=0
      data.map((m)=>{
        child={
          id:i++,
          imgUrl:"./images/rocket/"+m.name+".png",
          caption:m.name,
          distance:m.distance,
          clicked:clickPlanets.filter(f=>f.name===m.name).length>0?clickPlanets.filter(f=>f.name===m.name)[0].clicked:0
        }
        imgList.push(child)
      })
      setPlanetList(imgList)
    }).catch(err=>{
      console.log(err)
    })

    axios.get("https://findfalcone.herokuapp.com/vehicles").then(res=>{
      setVehicleList(res.data)
    }).catch(err=>{console.log(err)})

  },[clickCount])
  
  {    
    const coverfont={      
      color: 'White',
      fontSize: 'x-large'
    }
    const coverimg={
      height: '80px',
      width: '100px'
    }

    const submit=()=>{
      axios.post("https://findfalcone.herokuapp.com/token").then(res=>{
        console.log(res.data)
      }).catch(err=>{console.log(err)})
    }

    const radioChange=(e)=>{
      if(e.target.name==='card1')
        setRadio1(e.target.value)
      else if(e.target.name==='card2')
        setRadio2(e.target.value)
      else if(e.target.name==='card3')
        setRadio3(e.target.value)
      else if(e.target.name==='card4')
        setRadio4(e.target.value)
    }

    let imgHTML=[]
    planetList.map((m)=>
      imgHTML.push(
        <div className="col-2">
        {m.clicked!==0?<div className="arrow-center"><span>{m.clicked}</span></div>:''}
        <div className={m.clicked!==0?"zoom1":"zoom"}>
        <img className="rotate" src={m.imgUrl} style={coverimg} onClick={() => loadvehicles(m)} />
        <div style={coverfont}>{m.caption.toUpperCase()}</div>
        </div>
        </div>
        )
        )

        const CardRocket=(props)=>{
          
          return <div class="col-3" style={{padding:'10px 0px 0px 7px', color: 'white'}}>
          <div className="card card-primary">
          <div className="card-header">
          <h3 className="card-title">{props.caption}</h3>
          </div>
          <div className="card-body">

          {props.card}          

          </div>
          </div>
          </div>
        }

        const checkDuplicates=(list,incre)=>{
          let duplicates=[...list]
            list.map((m,index)=>{
              if(m.name===radio1)
                duplicates[index].total_no=duplicates[index].total_no-1
              if(m.name===radio2)
                duplicates[index].total_no=duplicates[index].total_no-1
              if(m.name===radio3)
                duplicates[index].total_no=duplicates[index].total_no-1
            })
          duplicates= duplicates.filter(f=>f.total_no!==0)
          console.log(duplicates)

          let vList1=[]
          let radioname="card"+incre
          let chkRadio;
          duplicates.map((m)=>{
            let url="./images/rocket/"+m.name+".png"
            vList1.push(
              <div>
          <div className="row">
          <div className="col-1" style={{marginTop:'1em'}}> <input type="radio" name={radioname} value={m.name} onChange={radioChange} Checked={m.name==="Space Pod"} /> </div>
          <div className="col-3">
          <img src={url} height="70" width="70" />
          </div>
          <div className="col-7" style={{marginTop: '1em'}}>
          <h5>{m.name.toUpperCase()}({m.total_no})</h5>
          </div>
          </div>
          </div>
            )            
          })
          if(clickCount===0) 
            setCard1(vList1)
          else if(clickCount===1) 
            setCard2(vList1)
          else if(clickCount===2) 
            setCard3(vList1)
          else if(clickCount===3) 
            setCard4(vList1)
       }

        const loadvehicles=(data)=>{
          if(clickCount===1 && radio1===undefined)
          {
            alert('Choose anyone Vehicle..!')
            return
          }
          else if(clickCount===2 && radio2===undefined)
          {
            alert('Choose anyone Vehicle..!')
            return
          }
          else if(clickCount===3 && radio3===undefined)
          {
            alert('Choose anyone Vehicle..!')
            return
          }

          if(clickCount===4||data.clicked!==0) return;
          let incre=clickCount+1
          let cap={clicked:incre,name:data.caption}
          setClickPlanets(clickPlanets.concat(cap))
          setClickCount(incre)
          let clk=[...planetList]
          clk[data.id].clicked=incre
          const vList=vehicleList.filter(f=>f.max_distance>=data.distance)
          checkDuplicates(vList,incre)          
        }

        return(
          <div>
          <div style={{backgroundImage: 'url("./images/rocket/cover.jpg")', backgroundPosition:'center'}}>
          <div className="row text-center">

          <div className="col-12 mb-2 text-left" style={coverfont}>
          FINDING FALCONE
          </div>

          {imgHTML}

          </div>

          </div>

          <div className="row" style={{padding:'0px 0px 0px 10px'}}>
          {card1.length>0?<CardRocket caption={clickPlanets[0].name} card={card1} />:''} 
          {card2.length>0?<CardRocket caption={clickPlanets[1].name} card={card2} />:''} 
          {card3.length>0?<CardRocket caption={clickPlanets[2].name} card={card3} />:''} 
          {card4.length>0?<CardRocket caption={clickPlanets[3].name} card={card4} />:''} 
          </div>

          <div className="btn btn-success text-center" onClick={submit}>Find</div>

          </div>
          )
      }
    }

    export default App;