import React from 'react'
import Nav from './nav'
import Footer from './footer'

class About extends React.Component
{
	render()
	{
   return(
   <div>
    <Nav id="about" active="about" />
    <section className="hero-wrap hero-wrap-2 js-fullheight" style={{backgroundImage: 'url(./images/bg_2.jpg)',marginTop:'-30%'}} data-stellar-background-ratio="0.5">
      <div className="overlay"></div>
      <div className="container">
        <div className="row no-gutters slider-text js-fullheight align-items-end justify-content-start">
          <div className="col-md-9 ftco-animate pb-5">
          	<p className=""><span className="mr-2"><a href="/">Home <i className="ion-ios-arrow-forward"></i></a></span> <span>FAQ <i className="ion-ios-arrow-forward"></i></span></p>
            <h1 className="mb-3 bread">Frequently Asked Questions</h1>
          </div>
        </div>
      </div>
    </section>


    <section style={{padding:'20px'}}>
      <h6>1. What is Intracity/local courier?</h6>  
      <h7>You can send your hand-carry parcel anywhere within city.</h7>  
      <br />
      <br />
      <h6>2. Can I do live track my parcel in Intracity?</h6>
      <h7>Tracking your Order is easy. Go to your list of Orders and select the Order you would like to track. You will be able to track your partner once they have been assigned to your Order. You could contact them by clicking driver info.
        <div>In an unfortunate event where your partner cancels, another partner should be assigned to complete the order for you.</div>
        <div>Do note that our delivery partners need to stay safe and obey rules. It is possible that they won't answer their calls while in transit, however, you will get a call back.</div></h7>
        <br />
        <h6>3. How can I do online payment?</h6>  
        <h7>Once you place your order it will be in cash on delivery.
          <div>Check with your order list you will find setup payment, choose your option to online payment.</div></h7>  
          <br />
          <h6>4. Cancel my order?</h6> 
          <h7>As a general rule you shall not be entitled to cancel your order once you have received confirmation of the same. If you cancel your order after it has been confirmed, Donkey cargo shall have a right to charge you cancellation fee of a minimum Rs.20 upto the order value.</h7>
          <br />
          <br />
          <h6>5. My order is taking longer time </h6>
          <h7>Your Order should be completed sooner. Due to external factors like high demand, road traffic may cause an additional delay.</h7>
          <br />
          <br />
          <h6>6. Your Order may be canceled for one of these reasons:</h6>
          <div>*There are no delivery partners available in your area</div>
          <div>*Your deliver partner might cancel your Order if they are unable to connect to you</div>
          <br />
          <h6>7. Can I place order for my friend?</h6>
          <h7>You could tap on 'Add a contact person' in your third step of booking process, near to apply coupon.</h7>
          <br />
          <br />
          <h6>8. I want to reschedule my task.</h6>
          <h7>Unfortunately, we currently do not have the option to schedule or reschedule your task. If you have already made the payment for your order, you may cancel the order and create a new order at the time of your choice.</h7>
          <br />
          <br />
          <h6>9. what is Item description and consignment value?</h6>
          <h7>Need to mention what product you are sending and value of that.</h7>
        </section>

        <Footer />
      </div>
      )
    }
  }

  export default About;