import React from 'react'
import { Link } from 'react-router-dom';

class Nav extends React.Component{
	constructor(props)
	{
		super(props);		
	}

	render()
	{
		return(
			<div>
<nav className="navbar navbar-expand-lg navbar-dark ftco_navbar bg-dark ftco-navbar-light" id="ftco-navbar">
	    <div className="container">
	      <Link className="navbar-brand" to="/">Donkey<span>{' '}Cargo</span></Link>
	      <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#ftco-nav" aria-controls="ftco-nav" aria-expanded="false" aria-label="Toggle navigation">
	        <span className="oi oi-menu"></span> Menu
	      </button>

	      <div className="collapse navbar-collapse" id="ftco-nav">
	        <ul className="navbar-nav ml-auto">
	          <li className={this.props.active==='home'?"nav-item active":"nav-item"}><a href="/" className="nav-link">Home</a></li>
	          <li className={this.props.active==='booking'?"nav-item active":"nav-item"}><a href="/Booking" className="nav-link">Booking</a></li>
	          <li className={this.props.active==='about'?"nav-item active":"nav-item"}><Link to="/About" className="nav-link">About</Link></li>
	          <li className={this.props.active==='contact'?"nav-item active":"nav-item"}><Link to="/Contact" className="nav-link">Contact</Link></li>
	        </ul>
	      </div>
	    </div>
	  </nav>
	  </div>
			)
	}
}

export default Nav;